.product{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 5px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  background-color: white;
  z-index: 1;
  
}
.product:hover{
        webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
        transform: scale(1.1);
}
.product-rating{
  display: flex;

}

.product > img {
  max-height: 5rem;
  width: 10rem;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  font-weight: 800;
}

.product-price {
  margin-top: 5px;
}

.product-info {
  height: 100px;
  margin-bottom: 15px;
  margin-top: 0px;

}